import { Component, AfterViewChecked, OnInit } from '@angular/core';
import { fromEvent } from 'rxjs';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewChecked{

  isBackground = false;
  isBackgroundChangeInit = false;

  ngOnInit() {
    if(!this.isBackgroundChangeInit){
    //  this.initBackgroundChange();
    }
  }

  ngAfterViewInit(): void {

    const content = document.querySelector('.parallax');
    const scroll$ = fromEvent(content, 'scroll');

    scroll$.subscribe(this.handleScroll.bind(this));
  }

  ngAfterViewChecked() {
 
  }

  handleScroll(event) {
    const scrollIndicator = <HTMLElement>document.getElementsByClassName("scrollIndicator")[0];

    const distance =  this.getDistanceBetweenElements(document.getElementsByClassName("navbar")[0], scrollIndicator);

    console.log('distance', distance);
    if(distance < 800) {
      scrollIndicator.style.display = "none";
    }
  }

  getDistanceBetweenElements(a, b) {
    const aPosition = this.getPositionAtCenter(a);
    const bPosition = this.getPositionAtCenter(b);
  
    return Math.hypot(aPosition.x - bPosition.x, aPosition.y - bPosition.y);  
  }

  getPositionAtCenter(element) {
    const {top, left, width, height} = element.getBoundingClientRect();
    return {
      x: left + width / 2,
      y: top + height / 2
    };
  }
}
